const formatData = (cur) => {
  let currency = ''
  let data = {}
  let i = 0
  Object.keys(cur).forEach(name => {
    data[i] = {
      name,
      price: cur[name]['USD'],
      img: `/img/${name}.png`
    }
    i++
  })

  Object.keys(data).forEach(key => {
    const name = data[key].name
    const price = data[key].price
    const imgSrc = data[key].img
    currency += `
      <tr>
      <td>${Number(key)+1}</td>
      <td>
        <div class="coin">
          <img src="${imgSrc}" alt="${name}">
          <p>Bitcoin <span>${name}</span></p>
        </div>
      </td>
      <td>$ ${price}</td>
    </tr>
    `
  })

  const tableBody = document.querySelector('#table-body')
  tableBody.innerHTML = currency
}

fetch('https://min-api.cryptocompare.com/data/pricemulti?fsyms=BTC,ETH,XRP,LTC,ADABCH,DOT,XMR,TRX,NEO&tsyms=USD&api_key={fa531fa2e07d2c7335fb8906264ccd1f2182f016c8f80f704b05540984ab8729}')
  .then(res => res.json())
  .then(data => formatData(data))